import React, { memo } from "react";
import ArchitectureDiagramSimple from "./ArchitectureDiagramSimple";
import ArchitectureDiagramRAG from "./ArchitectureDiagramRAG";

const ArchitectureDiagram = ({ architecture, projectId }) => {
  console.log("Current Project ID:", projectId); // Debug log

  // Check for string "1" or number 1
  if (projectId === "1" || projectId === 1) {
    //  CORRECT: No props passed. It uses its internal RAG data.
    return <ArchitectureDiagramRAG />;
  }

  //  CORRECT: Passes props for other projects
  return <ArchitectureDiagramSimple architecture={architecture} />;
};

export default memo(ArchitectureDiagram);
