// ============================================
// SMART CHIPS
// ============================================
const getChipsForTab = (tabName, projectName) => {
  const base = {
    "README.md": [
      `Summarize ${projectName}`,
      "What's unique about this?",
      "Biggest challenge?",
    ],
    "PROBLEM.md": [
      "Why was this important?",
      "Existing solutions?",
      "User pain point?",
    ],
    "SOLUTION.md": [
      "Explain the architecture",
      "Why this tech stack?",
      "How does data flow?",
    ],
    "tech-stack.json": [
      "Why these tools?",
      "Any trade-offs?",
      "What would you change?",
    ],
    "impact.md": ["Key metrics?", "How was success measured?", "What's next?"],
  };
  return base[tabName] || base["README.md"];
};

// ============================================
// NARRATOR SCRIPT
// ============================================
const getNarratorScript = (project) => {
  const d = project?.details || {};
  return [
    {
      title: "Intro",
      text: `Let me tell you about ${project.name}. ${project.description}`,
      tab: "README.md",
    },
    {
      title: "Problem",
      text: `So, here's the problem it solves. ${d.story?.problem || d.problem || "This project addresses a real-world challenge."}`,
      tab: "PROBLEM.md",
    },
    {
      title: "Solution",
      text: `And here's how I solved it. ${d.story?.solution || d.solution || "I built a solution using modern technologies."}`,
      tab: "SOLUTION.md",
    },
    {
      title: "Tech",
      text: `For the tech stack, I used ${project.languages?.map(l => l.name).join(", and ") || "modern technologies"}. Each was chosen for a specific reason.`,
      tab: "tech-stack.json",
    },
    {
      title: "Impact",
      text: `Now for the results. ${d.impact?.[0] || "This project made a meaningful impact."}`,
      tab: "impact.md",
    },
  ];
};
